.each-pinned-campaign {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 25px;
    margin-bottom: 25px;

    .img-container {
        width: 90%;
        height: 0;
        padding-bottom: 50%;
        background-size: cover;
        border-radius: 5px;
        cursor: pointer;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    }
    .post-info-container {
        width: 90%;
        cursor: pointer;

        h4 {
            margin: 15px 0 0 0;
            padding: 0;
            font-size: 18px;
            font-weight: 500;
            letter-spacing: 1px;
        }

        p {
            color: rgb(100, 100, 100);
            font-size: 12.5px;
            margin: 5px 0 0 0;
            padding: 0;

            .user-who-posted {
                background: none;
                color: rgb(100, 100, 100);

            }
        }
    }

    .post-stats-container {
        width: 90%;
        height: auto;
        margin: 0;
        padding: 0 10px 0 0px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        p {
            margin: 0;
            padding: 0;
            color: rgb(100, 100, 100);
            font-size: 10px;
        }
    }
}

@media screen and (max-width: 415px) {
    .each-pinned-gift-campaign {
        margin-top: 15px;

        .img-container {
            width: 90%;
        }
        .post-info-container {
            width: 85%;

            h4 {
                font-size: 16px;
            }
        }
        .post-stats-container {
            width: 85%;
        }
    }
}