body{margin:0;padding:0;height:100vh;font-family:'Varela Round', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

#nav-container{position:fixed;width:100vw;display:flex;flex-direction:row;justify-content:space-between;box-shadow:0 2px 2px -2px rgba(0,0,0,0.2);height:65px;z-index:10}#nav-container #logo-left{height:100%;display:flex;align-items:center;justify-content:center;width:25vw}#nav-container #logo-left a{margin:0;padding:0 10px 0 10px;border-left:2px solid #333;border-right:2px solid #333;color:#333;font-size:1.5rem;cursor:pointer;transition-duration:0.25s;text-decoration:none;text-transform:uppercase}#nav-container .navlink-container{height:100%;margin:0;padding:0;width:45vw;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}#nav-container .navlink-container a{margin:5px 0 0 0;padding:5px 20px;position:relative;font-size:1.1rem;font-family:'Sunflower', sans-serif;font-weight:500;color:#333;text-decoration:none;cursor:pointer;z-index:0}#nav-container .navlink-container a:before,#nav-container .navlink-container a:after{position:absolute;opacity:0;font-size:20px;bottom:5px;transition:all 0.3s}#nav-container .navlink-container a:before{content:'(';left:0px;-webkit-transform:translateX(10px);transform:translateX(10px)}#nav-container .navlink-container a:after{content:')';right:0px;-webkit-transform:translateX(-10px);transform:translateX(-10px)}#nav-container .navlink-container .is-active:before{content:'(';opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container .navlink-container .is-active:after{content:')';opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container .navlink-container a:hover:before{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container .navlink-container a:hover:after{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container .profile-container{width:25vw;height:100%;place-items:center}#nav-container .profile-container .login-button{margin:0;padding:0;padding:8px 30px 5px 30px;border:1px solid transparent;border-radius:10px;background:#333;color:#f8f8f8;font-size:1.15rem;cursor:pointer;transition-duration:0.25s;text-decoration:none}#nav-container .profile-container .login-button:hover{background:transparent;color:#333;border:1px solid #333}#nav-container .profile-container .logout-button{margin:0;padding:0;padding:8px 30px 5px 30px;border:1px solid transparent;border-radius:10px;background:#f8f8f8;color:#333;font-size:1.15rem;cursor:pointer;transition-duration:0.25s;text-decoration:none}#nav-container .profile-container .logout-button:hover,#nav-container .profile-container .logout-button:focus{color:#f8f8f8;background:transparent;border:1px solid #f8f8f8}#nav-container .profile-container .is-active{color:#333;background:transparent;border:1px solid #333}@media screen and (max-width: 415px){#nav-container{display:none}}

nav{display:none}@media screen and (max-width: 415px){nav{display:grid;place-items:center;text-align:center;grid-template-columns:auto auto auto auto auto;bottom:0;position:fixed;width:100vw;height:9vh;background-color:white;z-index:10;box-shadow:0px -2px 2px -2px rgba(0,0,0,0.2)}nav .link-container{grid-row:auto}}

.login-container{width:100vw;height:auto;margin-top:100px;display:grid;place-items:center}.login-container form{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.login-container form label{font-size:2.5rem}.login-container form input{margin-top:50px;padding-left:10px;border:none;border-bottom:1px solid grey;background-color:transparent;font-size:1.25rem}.login-container form input:focus{outline:none}.login-container form button{margin-top:50px;padding:10px 30px;border:1px solid #333;border-radius:10px;color:#f8f8f8;background-color:#333;font-size:1.5rem;cursor:pointer;transition-duration:0.25s}.login-container form button:hover{color:#333;background-color:#f8f8f8}.login-container form p{margin-top:25px}

.register-container{width:100vw;height:auto;padding-top:50px;display:grid;place-items:center}.register-container form{height:auto;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;background-color:white;box-shadow:rgba(60,64,67,0.3) 0px 1px 2px 0px,rgba(60,64,67,0.15) 0px 2px 6px 2px;padding:50px 40px;border-radius:15px}.register-container form label{font-size:2.5rem}.register-container form input{width:40vw;height:50px;margin-top:50px;padding-left:10px;border:none;border-radius:0;border-bottom:1px solid grey;background-color:transparent;font-size:1.25rem}.register-container form input:focus{outline:none}.register-container form h2{font-family:'Orbitron', 'sans-serif';margin-top:50px}.register-container form .name-container{margin:0;padding:0;width:40vw;display:flex;flex-direction:row;justify-content:space-between}.register-container form .name-container input{width:45%}.register-container form .bias-container{width:40vw;height:auto;display:grid;place-items:center;grid-template-columns:repeat(2, minmax(0, 1fr))}.register-container form .bias-container input[type="radio"]{opacity:0;position:fixed;width:0}.register-container form .bias-container label{text-align:center;width:90%;font-size:1.75vw;font-family:'Orbitron', 'sans-serif';margin-bottom:25px;padding:10px 10px;border:2px solid black;border-radius:10px;cursor:pointer}.register-container form .bias-container input[type="radio"]:checked+label{border:2px solid crimson;color:crimson}.register-container form button{margin-top:50px;padding:10px 30px;border:1px solid #333;border-radius:10px;color:#f8f8f8;background-color:#333;font-size:1.5rem;cursor:pointer;transition-duration:0.25s}.register-container form button:hover{color:#333;background-color:#f8f8f8}

#infocarousel-container{margin-top:75px}#infocarousel-container #ic-description-container{width:80vw;left:0;right:0;margin:0 auto}#infocarousel-container #ic-description-container h1{margin:0;padding:0;margin-left:1vw;margin-top:50px;margin-bottom:25px;font-family:'Orbitron', 'sans-serif'}#infocarousel-container .info-carousel{margin-bottom:25px;width:80vw;height:40vw;border-radius:30px;box-shadow:rgba(17,12,46,0.15) 0px 48px 100px 0px}#infocarousel-container .info-carousel img{height:100vh;width:auto}#infocarousel-container .description{display:none}.swiper-button-prev{color:white;left:0;padding-left:25px}.swiper-button-next{color:white;right:0;padding-right:25px}@media screen and (max-width: 900px){#infocarousel-container{margin-top:50px}#infocarousel-container #ic-description-container{width:90vw}#infocarousel-container #ic-description-container h1{font-size:1.5rem}#infocarousel-container .info-carousel{width:90vw;height:60vw}#infocarousel-container .info-carousel img{width:auto;height:100%}#infocarousel-container .description{display:block;width:80vw;left:0;right:0;margin:0 auto}#infocarousel-container .description h1{font-family:"Montserrat", 'sans-serif';font-size:1.75rem;font-weight:700}#infocarousel-container .description p{font-family:"Montserrat", 'sans-serif';font-size:0.85rem;letter-spacing:1px;margin:0;padding:0;font-weight:300}.swiper-button-next,.swiper-button-prev{display:none}}@media screen and (max-width: 415px){#infocarousel-container{margin-top:25px}}

#dashboard-title{margin:0;padding:0;margin-left:11vw;margin-top:100px;margin-bottom:25px;font-family:'Orbitron', 'sans-serif'}#dashboard-container{left:0;right:0;margin:auto;width:80vw;height:auto;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:space-between;align-items:flex-start}#dashboard-container .Gifts{width:42.5vw;height:35vw;display:flex;flex-direction:column;justify-content:space-between}#dashboard-container .Gifts .top-user{width:100%;height:35%;display:grid;place-items:center;background-color:rgba(0,0,0,0.25);border-radius:30px;color:white}#dashboard-container .Gifts .top-user div{width:90%;height:70%;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}#dashboard-container .Gifts .top-user div h1{font-size:3rem;padding:10px 20px;border:2px solid black;border-radius:50%}#dashboard-container .Gifts .top-user div .description{display:flex;flex-wrap:wrap;flex-direction:row;justify-content:center;align-items:center}#dashboard-container .Gifts .top-user div .description p{margin:0;padding:0;width:90%}#dashboard-container .Gifts .top-user div .description div{width:90%;height:17.5px;background-color:white;border-radius:20px}#dashboard-container .Gifts .whatever{width:100%;height:60%;display:grid;place-items:center;background-color:rgba(0,0,0,0.25);border-radius:30px}@media screen and (max-width: 910px){#dashboard-title{font-size:1.5rem;margin-left:6vw}#dashboard-container{width:90vw}#dashboard-container .Gifts{width:100%;height:auto}#dashboard-container .Gifts .top-user{height:35vw;margin-top:30px}#dashboard-container .Gifts .top-user div h1{font-size:2rem;padding:11.5px 17.5px;border:2px solid black;border-radius:50%}#dashboard-container .Gifts .top-user div .description p{font-size:0.65rem}#dashboard-container .Gifts .top-user div .description div{width:90%;height:12px;background-color:white;border-radius:20px}#dashboard-container .Gifts .whatever{height:50vw;margin-top:30px}}

#top-donations{display:grid;place-items:center;margin:0;padding:0;width:35vw;height:35vw;background-color:#f8f8f8;border-radius:30px;box-shadow:rgba(17,12,46,0.15) 0px 48px 100px 0px}#top-donations .chart-title-container{width:100%;display:grid;place-items:center}#top-donations .chart-title-container h3{margin:15px 0 0 0;padding:0;color:#4d4d4d;font-size:1.15rem;font-family:'Varela Round', sans-serif}#top-donations .chart-button-container{width:100%;height:auto;display:flex;flex-direction:row;justify-content:space-evenly;margin-bottom:10px}#top-donations .chart-button-container .see_chart_button{font-size:1.5rem;font-family:'Varela Round', sans-serif;margin:0;padding:10px 30px;border:1px solid black;border-radius:10px;background:#f8f8f8;cursor:pointer}#top-donations .chart-button-container .see_data_button{font-size:1.5rem;font-family:'Varela Round', sans-serif;color:white;margin:0;padding:10px 30px;border:none;border-radius:10px;background:#333;cursor:pointer}@media screen and (max-width: 910px){#top-donations{width:100%;min-height:400px}#top-donations .chart-title-container h3{font-size:1rem}#top-donations .chart-button-container .see_chart_button{font-size:1rem;border:1px solid black}#top-donations .chart-button-container .see_data_button{font-size:1rem}}@media screen and (max-width: 410px){#top-donations{min-height:375px}#top-donations .chart-title-container h3{font-size:0.75rem}#top-donations .chart-button-container{margin-bottom:7.5px}}

#landing-page{width:100vw}#landing-page .particle-background{display:none;position:absolute;width:100vw;height:100vh;z-index:-10}@media screen and (max-width: 910px){.particle-background{height:100vh}}

#donation-container{margin-top:100px}#donation-container #header-container{display:flex;width:80vw;left:0;right:0;margin:0 auto}#donation-container #header-container h1{width:400px;margin-top:0;margin-bottom:0;padding:0;margin-left:1vw;margin-right:auto;display:inline-block;font-family:'Orbitron', 'sans-serif'}#donation-container #header-container .sort{display:flex;margin-left:auto;padding-right:0px;justify-content:flex-end;align-items:flex-end}#donation-container #header-container .sort:hover{cursor:pointer;font-weight:bold}@media screen and (max-width: 910px){#donation-container #header-container{width:90vw}#donation-container #header-container h1{font-size:1.5rem}}

.each-pinned-campaign{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;margin-top:25px;margin-bottom:25px}.each-pinned-campaign .img-container{width:90%;height:0;padding-bottom:50%;background-size:cover;border-radius:5px;cursor:pointer;box-shadow:rgba(0,0,0,0.1) 0px 4px 12px}.each-pinned-campaign .post-info-container{width:90%;cursor:pointer}.each-pinned-campaign .post-info-container h4{margin:15px 0 0 0;padding:0;font-size:18px;font-weight:500;letter-spacing:1px}.each-pinned-campaign .post-info-container p{color:#646464;font-size:12.5px;margin:5px 0 0 0;padding:0}.each-pinned-campaign .post-info-container p .user-who-posted{background:none;color:#646464}.each-pinned-campaign .post-stats-container{width:90%;height:auto;margin:0;padding:0 10px 0 0px;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.each-pinned-campaign .post-stats-container p{margin:0;padding:0;color:#646464;font-size:10px}@media screen and (max-width: 415px){.each-pinned-gift-campaign{margin-top:15px}.each-pinned-gift-campaign .img-container{width:90%}.each-pinned-gift-campaign .post-info-container{width:85%}.each-pinned-gift-campaign .post-info-container h4{font-size:16px}.each-pinned-gift-campaign .post-stats-container{width:85%}}

.pinned-container{margin:75px 0 0 0;border-radius:5px;width:82.5vw;height:auto}.pinned-container .swiper-button-prev{margin-left:25px;padding:30px;border-radius:50%;background-color:rgba(0,0,0,0.25)}.pinned-container .swiper-button-next{margin-right:25px;padding:30px;border-radius:50%;background-color:rgba(0,0,0,0.25)}.pinned-container .nobias-exists{display:grid;place-items:center;width:100%;height:200px;color:#797979;border-radius:5px}.pinned-container .bias-exists{margin:0;width:100%;height:auto}@media screen and (max-width: 900px){.pinned-container{margin:50px 0 0 0;width:90vw}}@media screen and (max-width: 550px){.pinned-container{width:97vw}}@media screen and (max-width: 415px){.pinned-container{width:100vw;border-radius:0;margin:50px 0 0 0;padding:0;box-shadow:none}.pinned-container h1{margin:0 0 0 3%;font-size:1.5rem}.pinned-container h1 span{font-size:1.75rem;color:red;border-radius:50px}}

.each-component-container{width:90%;height:175px;margin:25px auto 0 auto;border-radius:5px;display:flex;justify-content:center;position:relative;overflow:hidden;box-shadow:rgba(0,0,0,0.06) 0px 2px 4px;transition:all .3s ease-in-out;cursor:pointer}.each-component-container h3{margin:10px auto;align-self:flex-end;font-family:"Do Hyeon", 'sans-serif';font-size:25px;color:white;letter-spacing:2px;z-index:1}.each-component-container .background-gradient{position:absolute;bottom:0;width:100%;height:35%;background:linear-gradient(180deg, rgba(255,255,255,0) 0%, #000 100%);z-index:0}.each-component-container:hover{box-shadow:rgba(0,0,0,0.22) 0px 19px 43px;-webkit-transform:translate3d(0px, -1px, 0px);transform:translate3d(0px, -1px, 0px)}@media screen and (max-width: 600px){.each-component-container{height:150px}.each-component-container h3{font-size:20px}}

.component-grid{align-items:start}

.button-container{width:auto;height:auto;display:inline-block}.button-container .category-nav-active,.button-container .category-nav-not-active{margin:0 0 9px 25px;background:black;color:white;padding:7px 15px 5px 15px;border:1px solid transparent;border-radius:5px;text-transform:none;text-decoration:none;font-family:"Sunflower", "sans-serif";font-size:1rem;font-weight:600;letter-spacing:1px;transition-duration:0.25s;cursor:pointer}.button-container .category-nav-active,.button-container .category-nav-not-active:hover{background:transparent;color:black;border:1px solid black;box-shadow:rgba(0,0,0,0.1) 0px 4px 12px}

.all-container{width:81.75vw}.all-container .categories-container{width:97.25%;display:flex;flex-direction:row;justify-content:space-between;align-items:center;left:0;right:0;margin:0 auto}@media screen and (max-width: 900px){.all-container{width:90vw}}@media screen and (max-width: 550px){.all-container{width:97vw}}


.App{height:auto;overflow:hidden}.App .navigation-margin{height:65px}.App .Content{width:100vw;display:grid;place-items:center;padding-bottom:100px}@media screen and (max-width: 900px){.login-button{margin:0;padding:8px 20px 5px 20px;background-color:white;color:black;border:1px solid transparent;border-radius:10px;font-size:25px;cursor:pointer;transition-duration:0.25s;text-decoration:none}.login-button:hover,.login-button:focus{background:transparent;color:#f8f8f8;border:1px solid #f8f8f8}.logout-button{margin:0;padding:8px 20px 5px 20px;background-color:white;color:black;border:1px solid transparent;border-radius:10px;font-size:25px;cursor:pointer;transition-duration:0.25s;text-decoration:none}}

